/* Navigation
-------------------------------------------------------------- */
.navigation {
  position: relative;
  height: 100%;
  padding: 20px 0;
  /* Navigation hamburger btn
  -------------------------------------------------------------- */
  /* Navigation social links
  -------------------------------------------------------------- */ }
  .navigation__btn {
    display: block;
    margin: 0 auto;
